import { getDictionary } from '@/dictionaries';
import { SupportedLanguage } from '@/models/locale';

interface EventProps {
  params: Promise<{ slug: string; lang: SupportedLanguage }>;
}

export default async function Event(props: EventProps) {
  const params = await props.params;
  const dictionary = await getDictionary(params.lang);

  const id = parseInt(params.slug, 10);
  if (isNaN(id)) {
    return <div>Etkinlik bulunamadi</div>;
  }

  /* eslint-disable */
  // ... diğer importlar ve kodlar
  console.log('yazdir sayfası render edildi!' + dictionary);
  // ... diğer kodlar
  /* eslint-enable */
}

